@import "./variables";

.pure-menu {
	// box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

.pure-menu-heading,
.pure-menu-link {
	padding: 0.8em 1em;
}

.pure-g [class*="pure-u"] {
	font-family: $fontFamily;
}

.button-error,
.button-secondary,
.button-success,
.button-warning {
	color: white;
	border-radius: 4px;
	text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}

.button-success {
	background: rgb(28, 184, 65);
	/* this is a green */
}

.button-error {
	background: rgb(202, 60, 60);
	/* this is a maroon */
}

.button-warning {
	background: rgb(223, 117, 20);
	/* this is an orange */
}

.button-secondary {
	background: rgb(66, 184, 221);
	/* this is a light blue */
}



