@import "../../assets/styles/variables";

.sidebar-menu {
	width: $menuWidth;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	background-color: #252a3a;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
	// 
	.user-box {
		text-align: center;
		margin: 40px auto;
		display: inline-block;
		text-decoration: none;

		.avatar {
			width: 80px;
			height: 80px;
		}

		.email {
			color: #f2f2f2;
			font-size: 12px;
		}
	}

	.main-menu {
		overflow: hidden;
		transition: all 0.5s;

		> h4 {
			margin: 10px 15px;
			color: #4b7197;
		}
	}

	.pure-menu-item {
		margin: 2px auto;

		.pure-menu-link {
			padding-left: 0.5em;
			color: #fff;
			cursor: pointer;

			&.router-link-active,
			&:focus,
			&:hover {
				background-color: #373c5a;
			}
		}

		&.menu-item-logout {
			display: none;

			.fa {
				color: #fb4b4b;
				margin-right: 5px;
				font-size: 20px;
				vertical-align: text-top;
			}
		}
	}

	.oops-menu {
		display: none;
		position: absolute;
		right: 10px;
		top: 10px;

		.pure-menu-item {
			cursor: pointer;
			position: absolute;
			right: 0;
			top: 0;
			border-bottom: none;
		}

		.custom-icon.icon-bars {
			background-color: #fff;

			&:after,
			&:before {
				background-color: #fff;
			}
		}

		.custom-icon.icon-times {
			&:after,
			&:before {
				background-color: #fff;
			}
		}
	}

	.item-logout {
		position: absolute;
		bottom: 40px;
		right: 55px;
		width: 40px;
		height: 40px;
		line-height: 40px;
		text-align: center;
		cursor: pointer;
		display: block;

		&:hover {
			.fa {
				transform: rotateZ(180deg);
				color: #616161;
			}
		}

		.fa {
			font-size: 24px;
			transition: all 0.3s ease;
			color: #fb4b4b;
		}
	}
}
@media screen and (max-width: 768px) {
	.sidebar-menu {
		width: 100%;
		right: 0;
		bottom: initial;
		height: 50px;
		z-index: 10;

		.user-box {
			margin: 10px;
			text-align: left;

			.avatar {
				width: 30px;
				height: 30px;
				vertical-align: middle;
			}
		}

		.pure-menu-list {
			background-color: #404861;
		}

		.pure-menu-item {
			border-bottom: 1px solid #566080;

			&.menu-item-logout {
				display: block;
			}

			.pure-menu-link {
				color: #bbb;

				&.router-link-active {
					background-color: #323b56;
					color: #fff;
				}
			}
		}

		.main-menu {
			height: 0;
			text-align: center;
		}

		.oops-menu {
			display: block;
		}

		&.show {
			.mask {
				position: fixed;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
			}
		}

		.item-logout {
			display: none;
		}
	}
}
@media screen and (max-width: 568px) {}



