* {
	box-sizing: border-box;
}

body,
html {
	-webkit-font-smoothing: antialiased;
	font-family: $fontFamily;
	color: #333;
	// user-select: none;
	// -webkit-user-select: none;
	font-size: 14px;
	font-family: 'Helvetica Neue',Helvetica,Arial,sans-serif;
}

li,
ul {
	text-decoration: none;
	list-style: none;
	margin: 0;
	padding: 0;
}

.text-right {
	text-align: right;
}

a[title=''] {
	display: none;
}

.text-success {
	color: green;
}

.text-error {
	color: gray;
}



